@import url(https://fonts.googleapis.com/css2?family=Readex+Pro:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
:root {
    --yellowgreen: #9acd32;
    --yellowgreen-low:#9acd32;
    --white : #ffffff;
    --black-low: #0d1321;
    --black-high: #141724;
    --black-body:#050b17;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
}
html{
    overflow-x: hidden;
    scroll-behavior: smooth;
    -webkit-tap-highlight-color: transparent;
}
body{
    overflow-x: hidden;
    background: #050b17;
    background: var(--black-body) ;
    -webkit-tap-highlight-color: transparent;
}
.body{
    max-width: 1400px;
}
#loading_start{
    background:#050b17;
    background:var(--black-body) ;
    width: 100%;
    height: 100vh;
    z-index: 100;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    padding: 0 0;
    margin: 0 0;
}

#loading_start p{
    font-size: 2rem;
    color: #9acd32;
    color: var(--yellowgreen);
    padding-right: 1rem;
    position: relative;
    top: -5px;
}

@media only screen and (max-width: 280px) {
    html{
        overflow-x: scroll;
    }
    body{
        overflow-x: scroll;
    }
    .body{
        min-width: 300px;
    }
}




.nav{
    top: 0;
    z-index: 9;
    width: 100%;
    display: flex;
    position: fixed;
    overflow: hidden;
    padding: 0.8rem;
    background-color: var(--black-body);
    /* background-color: aliceblue; */
    background-color: var(--black-low);

    font-family: "Montserrat", sans-serif;
}
.nav-sticky{
    background-color: var(--black-low);
    /* background-color: aliceblue; */

}
.nav-space{
    width: 100%;
    height: 7vmax;
}
.nav-mobile{
    display: none;
}
.nav-mobile-btn{
    display: none;
}


.scroll-up-btn i {
    display: none;    
}
.scroll-up-btn i{
    color: yellowgreen;
    font-weight: bold;
    cursor: pointer;
    font-size: 1.4rem;
    padding: 0.7rem 0.7rem;
    margin: 0.5rem;
    z-index: 7;
    position: fixed;
    bottom: 1.5rem;
    right: 1.5rem;
    border-radius: 50%;
    background: var(--black-high);
    background-color: var(--black-low);

    /* background-color: aliceblue; */

    border: 0.1rem solid yellowgreen;  
}
.scroll-up-btn .show{
    display: block;   
}

.nav a{
    list-style: none;
    text-decoration: none;
    padding: 0;
    margin: 0;
}

.nav h2{
    font-size: 2rem;
    padding: 0.2rem 3rem;
    color: var(--white);
    cursor: pointer;
}
.nav h2 span{
    color: yellowgreen;
}



.nav ul{
    list-style: none;
    text-decoration: none;
    display: flex;
    position: relative;
    left: 20rem;
}
.nav ul li{
    padding: 0.5rem 1rem;
    cursor: pointer;
}
.nav ul li a{
    font-size: 1rem ;
    color: yellowgreen;
    list-style: none;
    text-decoration: none;
}
.nav ul li i{
    font-size: 1rem ;
    padding-right: 0.6rem;
}
.nav ul li a i{
    color: var(--white);
}

.nav ul li:hover{ 
    border: yellowgreen;
    border-radius: 0.3rem;
}
.nav ul li:hover>a { 
    color: yellowgreen;
    font-weight: bold;
    text-shadow: 10px 5px 20px yellowgreen;
}

.nav ul li:hover>a>i { 
    color: var(--aqua);
    font-weight: bold;
    text-shadow: 10px 5px 20px yellowgreen;
}

.nav .active{ 
    /* color: var(--aqua); */
    font-weight: bold;
    border-bottom: 0.15rem solid yellowgreen;
    padding-top: 0.6rem;
    padding-bottom: 0.6rem;
}
.nav .active i{ 
    color: yellowgreen;
    font-weight: bold;
}

@media only screen and (max-width: 1200px) {
    .nav ul{
        left: 8rem;
    }
  }

@media only screen and (max-width: 1000px) {
    .nav-mobile{
        padding: 0;
        margin: 0;
        width: 100%;
        display: flex;
        overflow: hidden;
        position: fixed;
        top: 0;
        z-index: 8;
        background-color: var(--black-body);
        font-family: "Montserrat", sans-serif;
    }
    .nav-mobile-sticky{
        background-color: var(--black-low);
    }
    
    .nav-mobile a{
        list-style: none;
        text-decoration: none;
        padding: 0;
        margin: 0;
    }
    .nav-mobile h2{
        font-size: 2rem;
        padding: 1.5rem 2rem;
        color: var(--white);
        cursor: pointer;
    }
    .nav-mobile h2 span{
        color: yellowgreen;
    }
    .nav-mobile-btn{
        cursor: pointer;
        display: block;
        color: var(--white);
        position: fixed;
        top: 2rem;
        right: 2rem;
    }
    .nav-mobile-btn span i{
        font-weight: bold;
        font-size: 1.4rem;
    }
    .nav-mobile-btn span .btn_active::before{
        content: "x";
    }
    .scroll-up-btn i{
        position: fixed;
        bottom: 1rem;
        right: 1rem; 
    }


    .nav{
        top: 6.5rem;
        right: -300%;
        width: auto;
        display: block;
        position: fixed;
        border-radius: 0.5rem;
        transition: all 0.7s ease;
        background-color: var(--black-low);
        -webkit-animation: 4s ease;
                animation: 4s ease;
    }
    nav .nav_active{
        right: 1rem;
        -webkit-animation: navbar 1s ease-in-out;
                animation: navbar 1s ease-in-out;
    }
    @-webkit-keyframes navbar{
        0%{
            right: -50%;
        }
        50%{
            right: 1rem;
        }
    }
    @keyframes navbar{
        0%{
            right: -50%;
        }
        50%{
            right: 1rem;
        }
    }
    .nav h2{
        display: none;
    }   
    .nav ul{
        display: block;
        left: 0%;
    }
    .nav ul li{
        padding: 1rem 2rem;
    }
    .nav ul li a i{
        color: var(--white);
    }
    .nav ul li:hover{ 
        border: none;
        border-radius: none;
    }
    .nav ul li:hover>a { 
        text-shadow: none;
    }
    .nav ul li:hover>a>i { 
        text-shadow: none;
    }
    .nav .active{ 
        color: yellowgreen;
        font-weight: bold;
        border-bottom: 0.15rem solid;
        padding-top: 0.6rem;
        padding-bottom: 0.6rem;
        padding-right: 0.6rem;
    }
    .nav .active i{ 
        color: yellowgreen;
        font-weight: bold;
    }
  }


  @media only screen and (max-width: 280px) {
    .nav-mobile-btn{
        top: 4rem;
    }

}





.home{
    height: 90vh;
    padding: 0rem 4.5rem;
    font-family: "Readex-Pro", sans-serif;
    background-image:image() ;
}
.home-flex{
    display: flex;
}
.home p{
    color:var(--white);   
    font-size: 1.6rem;
    padding-left: 0.2rem;
}
.home h1{
    padding: 0.2rem 0;
    color: var(--white);
    font-size: 4.5rem;
}
.home h2{
    color: var(--white);
    font-size: 2.2rem;
    padding-bottom: 40px;
}
.home h2 span{
    /* color: var(--aqua); */
    color: yellowgreen;
}
.home .left-content{
    padding: 7rem 0rem;
    width: 50%;
}
.home .left-content a{
    color: var(--white);
    font-size: 1.5rem;
    font-weight: bold;
    padding: 0.8rem 1.5rem;
    border-radius: 0.3rem;
    border: 0.15rem solid yellowgreen;
    text-decoration: none;
    list-style: none;
}
.home .left-content a:hover{
    color: var(--black-high);
    /* background: var(--aqua); */
    background:  yellowgreen;
    font-weight: bold;
}
.home .right-content{
    padding: 2rem 0rem;
    width: 50%;
    overflow-x: hidden;
    display: flex;
    margin: auto;
}

@media only screen and (max-width: 1000px) {
    .home{
        padding: 0rem 0rem;
        height: 80vh;
    }
    .home-flex{
        display: block;
    }
    .home .left-content{
        width: 100%;
        padding-left: 2rem;
        padding-top: 17vmax;
    }
    .home .right-content{
        position: relative;
        top: -500px;
        right: -200px;
        height: auto;
    }
  }
@media only screen and (max-width: 870px) {
    .home .right-content{
        position: relative;
        top: -400px;
        right: -250px;
    }
    .home .right-content img{
        width: 90%;
        height: 90%;
    }
  }
@media only screen and (max-width: 750px) {
    .home{
        height: auto;
    }
    .home p{  
        font-size: 1.2rem;
    }
    .home h1{
        font-size: 3.5rem;
    }
    .home h2{
        font-size: 1.7rem;
    }
    .home .left-content{
        padding-top: 15rem;
    }
    .home .left-content a{
        font-size: 1.2rem;
        padding: 0.6rem 1rem;
        border-radius: 0.2rem;
    }
    .home .right-content{
        position: absolute;
        top: 5rem;
        right: 50px;
    }
    .home .right-content img{
        width: 100%;
        height: 100%;
    }
  }
@media only screen and (max-width: 400px) {
    .home p{  
        font-size: 1.1rem;
    }
    .home h1{
        font-size: 3.2rem;
    }
    .home h2{
        font-size: 1.6rem;
    }
    .home .left-content a{
        font-size: 1rem;
        padding: 0.4rem 0.9rem;
        border-radius: 0.2rem;
    }
}

