.services{
    min-height: 90vh;
    padding: 1rem 3rem;
    font-family: "Readex-Pro", sans-serif;
    background-color: var(--black-body);
}
.services h1{
    color: var(--white);
    text-align: center;
    padding-bottom: 2rem;
}
.services .boxes{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; 
    grid-template-columns: repeat(3,auto);
}



@media only screen and (max-width: 1000px) {
    .services{
        padding: 1rem 0rem;
    }
    .services .boxes{
        grid-template-columns: 1fr 1fr; 
        grid-template-columns: repeat(2,auto);
    }
}

@media only screen and (max-width: 780px) {
    .services .boxes{
        grid-template-columns: 1fr; 
        grid-template-columns: repeat(1,auto);
    }
}




.services .card{
    text-align: center;
    margin: 1rem 1rem;
}
.services .card .box{
    padding: 1.5rem 1.5rem;
    border-radius: 1rem;
    height: 15rem;
    background: var(--black-high);
    cursor: pointer;
}
.services .card .box:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    z-index: -7;
}
.services .card .box i{
    /* color: var(--aqua); */
    color: yellowgreen;
    font-size: 2rem;
    padding-bottom: 1.5rem;
}
.services .card .box .heading{
    /* color: var(--aqua); */
    color: yellowgreen;
    font-size: 1.3rem;
    padding-bottom: 1rem;
    font-weight: bold;
}
.services .card .box .text{
    color: var(--white);
    font-size: 1rem;
    padding-bottom: 1rem;
    line-height: 1.5rem;
    text-align: left;
}

@media only screen and (max-width: 1000px) {
    .services .card{
        margin: 2rem 2rem;
    }

}
@media only screen and (max-width: 780px) {
    .services .card .box{
        height: auto;
    }
    .services .card{
        margin: 1rem 1.5rem;
    }
    .services .card .box:hover{
        -webkit-transform: scale(1.03);
                transform: scale(1.03);
        z-index: -7;
    }

}

.project{
    padding: 3rem 3rem;
    font-family: "Readex-Pro", sans-serif;
    background-color: transparent;
    height: auto;
}
.project h1{
    color: var(--white);
    text-align: center;
    padding-bottom: 2rem;
}
.project .text{
    color: var(--white);
    font-size: 1rem;
    padding: 1rem 1rem 2rem 1rem;
    line-height: 1.5rem;
    text-align:justify;
}


.owl-dots{
    text-align: center;
    margin-top: 20px;
}
.owl-dot{
    height: 13px;
    width: 13px;
    margin: 0 5px;
    outline: none !important;
    border-radius: 50%;
    border: 2px solid var(--aqua) !important;
    transition: all 0.3s ease;
}
.owl-dot.active{
    width: 35px;
    border-radius: 14px;
}
.owl-dot.active,
.owl-dot:hover{
    background: var(--aqua) !important;
}


@media only screen and (max-width: 1000px) {
    .project{
        padding: 3rem 1rem;
    }
}
.project .projectbox{
    margin: .5rem;
    border: 2px solid var(--black-body);
}
.project .projectbox img{
    border-top-left-radius: .5rem;
    border-top-right-radius: .5rem;
}
.project .projectbox .details{
    border-left: 2px solid yellowgreen;
    border-right: 2px solid yellowgreen;
    border-bottom: 2px solid yellowgreen;
    border-bottom-left-radius: .5rem;
    border-bottom-right-radius: .5rem;
    padding-bottom: 10px ;
    padding-top: 10px;
    display: flex;
}

.project .projectbox a i{
    color: yellowgreen;
    font-weight: bold;
    font-size: 1.5rem;
    font-weight: bold;
    padding-right: 20px;
    padding-left: 20px;
}
.project .projectbox p{
    color: var(--white);
    font-size: 1rem;
    font-weight: bold;
    margin:auto;
    padding-left: 12px;
}
